header {
    text-align: center;
    padding: 1em 0;
    background-color: rgb(3, 37, 53);
    margin-bottom: 2em;
}

header ul {
    width: 30%;
}

header li {
    list-style: none;
    font-size: 1.2em;;
}

header a {
    text-decoration: none;
    color: rgb(0, 255, 115);
}

header, ul {
    display: flex;
    justify-content: space-around;
}

header {
    justify-content: space-between;
}

header .title {
    display: flex;
    align-items: center;
    font-size: 1.6em;
    font-weight: bold;
    font-family: Arial, Helvetica, sans-serif;
    color: #fff;
    margin-left: 2em;
}
