@import url('https://fonts.googleapis.com/css2?family=Georama:wght@200&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Mukta:wght@200&display=swap');

html {
}

header {
  padding: 0;
  margin: 0;
}

#bg_content {
  background: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)),
    url(./bg.jpg);
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  color: #fff;
}

#header_main_content {
  width: 80%;
  margin: 0 auto;
  padding: 2rem 0;
}

.header_title {
  font-size: 72px;
  margin: 0;
}

.header_title {
  font-family: 'Georama', sans-serif;
}

.sub_body {
  width: 80%;
  margin: 0 auto;
}

.header_title .subtitle {
  font-size: 40px;
  font-weight: bold;
  color: lightskyblue;
}

.header_content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding: 2rem 0;
}

.header_content * {
  margin: 0 1rem;
}

.header_desc {
  width: 40%;
  padding: 1rem 1rem;
  font-size: 16 px;
}

.header_content strong {
  margin: 0;
  color: salmon;
}

.header {
  display: flex;
  justify-content: space-between;
  padding: 1rem 2rem;
  background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3));
  backdrop-filter: blur(8px);
}

.main_header_title {
  font-size: 32px;
}

.header .header_navbar {
  width: 20%;
  align-items: center;
  display: flex;
}

.header .header_navbar ul {
  display: flex;
  width: 100%;
  justify-content: space-around;
  list-style: none;
  margin: 0;
  padding: 0;
}

.header .header_navbar ul {
  margin: 0 0.5rem;
}

.header_navbar a {
  text-decoration: none;
  color: inherit;
  padding: 1rem;
}

.nostyle {
  display: none;
  padding: 1rem;
  text-align: center;
}

@media screen and (max-width: 1228px) {
  .header_title {
    font-size: 68px;
  }
}

@media screen and (max-width: 1171px) {
  .header_title {
    font-size: 64px;
  }

  .header_desc {
    font-size: 14px;
  }

  .header_title .subtitle {
    font-size: 36px;
  }
}

@media screen and (max-width: 1116px) {
  .header_title {
    font-size: 60px;
  }
}

@media screen and (max-width: 1060px) {
  .header_title {
    font-size: 54px;
  }

  .header_desc {
    font-size: 15px;
  }

  .header_title .subtitle {
    font-size: 32px;
  }
}

@media screen and (max-width: 975px) {
  .header_title {
    font-size: 50px;
  }

  .header_desc {
    font-size: 14px;
  }

  .header_title .subtitle {
    font-size: 28px;
  }

  .main_header_title {
    font-size: 28px;
  }
}

@media screen and (max-width: 919px) {
  .header_content {
    flex-direction: column;
    align-items: center;
  }

  .header_desc {
    margin-top: 1.5rem;
    width: 50%;
  }

  .header .header_navbar {
    width: 25%;
  }
}

@media screen and (max-width: 593px) {
  .header .header_navbar {
    width: 40%;
  }
}

@media screen and (max-width: 746px) {
  .sub_html {
    display: none;
  }

  .nostyle {
    display: block;
  }
}

ul a {
  color: salmon;
}

#scrollup_btn {
  display: none;
  justify-content: center;
  align-items: center;
  font-size: 32px;
  width: 50px;
  height: 50px;
  position: fixed;
  bottom: 25px;
  right: 25px;
  background-color: rgb(112, 112, 112);
  color: #fff;
  border-radius: 50%;
  cursor: pointer;
}
