.item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 8rem 0;
  flex-wrap: initial;
}

.item img {
  width: 50%;
  height: auto;
  border-radius: 30px;
}

.item .info {
}

.item *:not(a) {
  margin: 0 1rem;
}

.description {
  font-size: 20px;
}

.appName {
  font-size: 3em;
  font-family: 'Mukta', sans-serif;
  font-weight: bold;
}

.AppViews {
  margin-top: 3rem;
}
